:root {
  /*  C O L O R S  */
  --dark-magenta: hsl(300, 43%, 22%);
  --soft-pink: hsl(333, 80%, 67%);
  --gray-magenta: hsl(303, 10%, 53%);
  --light-magneta: hsl(300, 24%, 96%);
  --white: hsl(0, 0%, 100%);
}

/*  B A S I C   R E S E T  */
html {
  direction: ltr;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "League Spartan", sans-serif;
}

button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}
img::selection {
  background: transparent;
}

a {
  text-decoration: none;
}

main {
  min-height: 100vh;
  background-image: url(../images/bg-pattern-top-mobile.svg), url(../images/bg-pattern-bottom-mobile.svg);
  background-size: contain, contain;
  background-repeat: no-repeat, no-repeat;
  background-position: top left, bottom right;
  display: grid;
  place-content: center;
}
@media (min-width: 37.5rem) {
  main {
    background-image: url(../images/bg-pattern-top-desktop.svg), url(../images/bg-pattern-bottom-desktop.svg);
    background-size: 55% 40%, 80% 80%;
  }
}

.top h2 {
  color: var(--dark-magenta);
  font-size: 2.5rem;
  text-align: center;
  font-weight: 700;
  padding-inline: 1rem;
}
@media (min-width: 37.5rem) {
  .top h2 {
    text-align: left;
    font-size: 3rem;
    padding-inline: 0;
    width: 70%;
  }
}
.top p {
  text-align: center;
  color: var(--gray-magenta);
  font-size: 1.25rem;
  line-height: 1.3;
  padding-inline: 0.4rem;
  font-weight: 500;
}
@media (min-width: 37.5rem) {
  .top p {
    text-align: left;
    padding-inline: 0;
    width: 9 0%;
  }
}
.top-cards ul > li {
  padding: 0.8rem;
  background-color: #f7f2f8;
  border-radius: 10px;
  margin-bottom: 1rem;
  text-align: center;
  color: var(--dark-magenta);
  font-weight: 700;
  font-size: 1.1rem;
}
@media (min-width: 48rem) {
  .top-cards ul > li {
    padding: 1.3rem 2.3rem;
    width: 80%;
    margin-left: auto;
  }
  .top-cards ul > li:first-child {
    transform: translateX(-120px);
  }
  .top-cards ul > li:nth-child(2) {
    transform: translateX(-60px);
  }
}
.top-cards .stars {
  color: #f0953c;
  margin-bottom: 0.7rem;
  font-size: 1rem;
}
@media (min-width: 37.5rem) {
  .top-cards .stars {
    margin-bottom: 0;
    margin-right: 3rem;
  }
}
.top-cards .stars > * {
  margin-inline: 0.2rem;
}

.bottom .col-sm-4 {
  padding: 0.7rem 1rem;
}
@media (min-width: 37.5rem) {
  .bottom .col-sm-4:first-child {
    transform: translateY(-20px);
  }
  .bottom .col-sm-4:last-child {
    transform: translateY(20px);
  }
}
.bottom .item {
  background-color: var(--dark-magenta);
  border-radius: 8px;
  padding: 2rem;
  color: var(--white);
}
@media (min-width: 37.5rem) {
  .bottom .item {
    padding: 2.5rem;
  }
}
.bottom .item img {
  border-radius: 50%;
  width: 13%;
  margin-right: 1.5rem;
}
@media (min-width: 37.5rem) {
  .bottom .item img {
    width: 15%;
  }
}
.bottom .item h3 {
  font-weight: 500;
  margin-bottom: 0.5rem;
}
.bottom .item h4 {
  font-weight: 400;
  color: var(--soft-pink);
}
.bottom .item p {
  line-height: 1.3;
}
@media (min-width: 37.5rem) {
  .bottom .item p {
    font-size: 1.05rem;
  }
}